/*Menu*/
.navbar {
  overflow: hidden;
  background-color: #bebebe;
}

.navbar a {
  float: left;
  font-size: 15px;
  color: black;
  background-color: inherit;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 15px;  
  border: none;
  outline: none;
  color: black;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.navbar a:hover, .dropdown:hover .dropbtn {
  background-color:gray;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  color: black;
  background-color: #f9f9f9;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  background-color: gray;
}

.dropdown:hover .dropdown-content {
  display: block;
}

/* For mobile phones: */
@media only screen and (max-width: 1369px){
  .acontent-table table, thead, tbody, th, td, tr { 
		display: block;
  }
  
	.acontent-table thead tr { 
		display: none;
	}
	
	.acontent-table tr { 
    border: 1px solid black;
    margin-bottom: 30px;} 
	
	.acontent-table td { 
    border: none;
    color: #555555;
		position: relative;
  }
	
	.acontent-table td:before { 
    content: attr(data-label);
    color: black;
    padding: 10px;
    position: relative;
    text-align: left;
    white-space: nowrap;
  }
    
}
@media only screen and (max-width: 1050px) {
  .dropdown-content {
    width: 100%;
  }
  .wrap-logina{
    width: 100%;
}
  .dropdown-content a {
    width: 100%;
    text-align: center;
  }
  
  .dropdown {
    float: center;
    overflow: hidden;
    width: 100%;
  }

  .dropdown .dropbtn {
    position: relative;
    text-align: center;
    padding: 6px 6px;
    width: 100%;
  }

  .navbar a {
